@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;1,700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Lato', sans-serif;
  background-color: rgba(240, 240, 240, .3);
}

.catalog {
  display: flex;
  flex-direction: column;
  width: 95%;
  max-width: 1100px;
  margin: 20px auto;
}

.catalog__header {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.header__title {
  font-size: 34px;
  margin: 0 20px 40px 0;
  color: orange;
}

.header__title--brown {
  color: brown;
}

.header__input {
  font-family: "Lato", sans-serif;
  width: 200px;
  border: none;
  padding-bottom: 5px;
  font-size: 14px;
  cursor: pointer;
  outline: none;
  background-color: rgba(240, 240, 240, .3);
}

.header__input::-webkit-input-placeholder {
  color: #555;
}

.header__input:-ms-input-placeholder {
  color: #555;
}

.header__input::-ms-input-placeholder {
  color: #555;
}

.header__input::placeholder {
  color: #555;
}

.header__input-subtitle {
  padding-top: 5px;
  font-size: 14px;
  color: #555;
  height: 35px;
}

.header__line {
  height: 2px;
  background-color: orange;
}

.header__line--red {
  background-color: red;
}

.catalog__cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 20px 10px;
  font-size: 12px;
}

.card {
  margin: 0px 10px 10px;
}

.card__type-name {
  margin: 5px;
}

.card__title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 5px;
}

.card__type {
  font-weight: bold;
}

.card__name {
  font-size: 22px;
  font-weight: bold;
}

.catalog__loader {
  align-self: center;
}

.catalog__button {
  margin-top: 20px;
  align-self: center;
  outline: none;
  padding: 10px 30px;
  font-size: 28px;
  background-color: orange;
  color: white;
  border: none;
  transition: 0.3s;
}

.catalog__button:hover {
  background-color: brown;
}

@media (min-width: 550px) {
  .catalog__header {
    justify-content: space-between;
  }
}

.loader {
  width: 100%;
  height: 100;
  display: flex;
  justify-content: center;
  align-items: center;
}

